@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

* {
  box-sizing: border-box;
}
.styles_app__1LNKg {
    width: 100%;
    height: 100vh;
    margin: 0;
}

.styles_header__2qSeF {
    width: 100%;
    height: 10rem;
    padding-top: 2rem;
}

.styles_header__2qSeF h5 {
    float: left;
    margin-left: 10px;
}

.styles_miniCircle__32B7c {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: linear-gradient(360deg, rgba(132, 114, 213, 0.3), rgba(61, 130, 223, 0.2)), url(/static/media/bg.d17b956a.jpg);
    padding: 10px;
    float: left;
}

.styles_miniCircle__32B7c img {
    width: 45px;
}

.styles_center__3asL7 {
    width: 100%;
    height: auto;
    display: block;
    padding: 2rem 10rem;
}

.styles_circleGrid__3TWZR {
    display: grid;
    grid-template-columns: 33.3% 33.3% 33.3%;
}

.styles_circle__3wW1U {
    width: 300px;
    height: 300px;
    background: #000;
    border-radius: 50%;
    margin: 0 auto;
    padding: 5rem;
    text-align: center;
    color: white;
    position: relative;
    margin-bottom: 1rem;
}

.styles_blue__5MR_V {
    background: linear-gradient(90deg, rgba(24, 195, 243, 1), rgba(0, 117, 225, 1));
}

.styles_green__1paFa {
    background: linear-gradient(90deg, rgba(57, 204, 149), rgba(58, 205, 198));
}

.styles_red__392DB {
    background: linear-gradient(90deg, rgb(0, 200, 81), rgb(0, 126, 51))
}


.styles_error__3IHTi {
    background: crimson;
}

.styles_circle__3wW1U:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.styles_circle__3wW1U svg {
    font-size: 4rem;
    margin-bottom: 1rem;
}

.styles_circle__3wW1U h3 {
    font-size: 1.5rem;
}

.styles_center__3asL7 h1 {
    font-size: 2rem;
    font-weight: bold;
    color: teal;
}

.styles_center__3asL7 h4 {
    font-size: 1.5rem;
    display: block;
    font-weight: bold;
    margin-bottom: 2rem;
}


.styles_form__1dqCA {
    width: 100%;
    height: auto;
    display: block;
    padding: 2rem;
}

.styles_formContainer__3_KHJ {
    padding: .5rem;
}

.styles_formContainer__3_KHJ h3 {
    font-size: 1.5rem;
    color: teal;
    font-weight: bold;
    margin-bottom: 1rem;
}






